export type SessionStorageStatus = 'FOUND' | 'NOT_FOUND' | 'NO_CONSENT';

export interface ISessionStorageValue<T> {
    name: string;
    value: T | undefined;
    status: SessionStorageStatus;
}

export interface ISessionStorageContext {
    getDataFromStorage(key: string): object;
    setDataInStorage(key: string, value: object): void;
    removeDataFromStorage(key: string): void;
    getCheckoutCartData(key: string): object;
    setCheckoutCartData(key: string, value: object): void;
    removeCheckoutCartData(key?: string): void;
}
